import React from "react";
import "./Search.css";
import { Link } from "react-router-dom";
import Stories from "./Stories";
import People from "./People";
import { fetchStories, fetchPeople } from "services/searchService";
import { StickyContainer, Sticky } from "react-sticky";

const queryString = require("query-string");

export default class Search extends React.Component {
  constructor() {
    super();
    this.state = {
      stories: [],
      people: [],
      query: ""
    };
  }

  componentDidMount() {
    // To delay blog data send
    this.timeout = 0;

    /*
     * If query string is present in url i.e. /users?q=tamang
     */
    this._searchInitializer(this.props.history.location.search);
  }

  componentWillReceiveProps(nextProps) {
    /*
      When user navigate forward and backward from browser
    */
    this._searchInitializer(nextProps.history.location.search);
  }

  _searchInitializer(qString) {
    const parsed = queryString.parse(qString);
    const query = parsed.q;
    if (query) {
      this.setState({ query });
      this.fetchSearchData(query);
    }
  }

  onSearch = e => {
    this.setState({ query: e.target.value }, () => {
      if (this.timeout) clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        const { query } = this.state;
        if (query !== "") {
          // if input is not empty
          this.fetchSearchData(query);
          this.props.history.push(
            `${this.props.history.location.pathname}?q=${query}`
          );
        } else {
          this.setState({ stories: [], people: [] });
          this.props.history.push(this.props.history.location.pathname);
        }
      }, 800);
    });
  };

  fetchSearchData(query) {
    fetchStories(query).then(res => {
      this.setState({ stories: res.data });
    });
    fetchPeople(query).then(res => {
      this.setState({ people: res.data });
    });
  }

  __renderContent(url) {
    if (url === "/search") {
      return <Stories stories={this.state.stories} />;
    } else if (url === "/search/users") {
      return <People people={this.state.people} />;
    } else {
      return <h1 style={{ width: 100 + "%" }}>Page not found</h1>;
    }
  }

  _renderTabs(url) {
    let storyLink;
    let userLink;

    if (this.state.query === "") {
      storyLink = "/search";
      userLink = "/search/users";
    } else {
      storyLink = `/search?q=${this.state.query}`;
      userLink = `/search/users?q=${this.state.query}`;
    }

    if (url === "/search") {
      return (
        <ul className="list-inline">
          <li>
            <Link to={storyLink} className="active">
              Stories
            </Link>
          </li>
          <li>
            <Link to={userLink}>People</Link>
          </li>
        </ul>
      );
    } else if (url === "/search/users") {
      return (
        <ul className="list-inline">
          <li>
            <Link to={storyLink}>Stories</Link>
          </li>
          <li>
            <Link to={userLink} className="active">
              People
            </Link>
          </li>
        </ul>
      );
    }

    return (
      <ul className="list-inline">
        <li>
          <Link to={storyLink} className="active">
            Stories
          </Link>
        </li>
        <li>
          <Link to={userLink}>People</Link>
        </li>
      </ul>
    );
  }

  render() {
    const url = this.props.location.pathname;
    return (
      <section className="mjl-container p-search">
        <input
          value={this.state.query}
          autoComplete="off"
          placeholder="Search Threadly"
          onChange={this.onSearch}
          className="text-input"
        />

        <div className="s-wrapper">
          <nav>{this._renderTabs(url)}</nav>

          <div className="s-content d--flex">
            {this.__renderContent(url)}

            <div className="s-adv">
              <StickyContainer>
                <Sticky>
                  {({ distanceFromTop }) => {
                    let style = {};
                    if (distanceFromTop < 0)
                      style = { top: 8 + "px", position: "fixed" };
                    return (
                      <div style={style}>
                        <span className="adv-t text--muted">
                          <img
                            style={{ height: 14 + "px", marginRight: 5 + "px" }}
                            src="data:image/svg+xml;utf8;base64,"
                            alt=""
                          />
                          Sponsored
                        </span>
                        <div>
                          <a
                            href="https://www.visitnepal.travel"
                            rel="noopener noreferrer"
                            target="_blank"
                          >
                            <img
                              src="/static/images/ghyampo-sb.png"
                              alt="ghyampo store banner"
                            />
                          </a>
                        </div>
                      </div>
                    );
                  }}
                </Sticky>
              </StickyContainer>
            </div>
          </div>
        </div>
      </section>
    );
  }
}
